body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #4baa86;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.card {
  background: #fff;
  padding: 40px 20px;
  border-radius: 0;
}

.heading {
  color: #4baa86;
  font-weight: 700;
  font-size: 3rem;
  text-shadow: 1px 1px 2px;
}

.amount {
  font-weight: 700;
  font-size: 60px;
  margin-left: 35px;
  font-style: italic;
}

.btn {
  background: #4baa86;
  font-weight: 700;
  box-shadow: 1px 1px 15px #4baa86;
}

.btn:active,
.btn:hover {
  background: #4baa86 !important;
  border-color: #4baa86 !important;
}

.price {
  margin-top: 10px;
}

sup {
  color: #4baa86;
  font-style: italic;
  font-weight: normal;
}

sub {
  font-weight: normal;
  font-size: 22px;
  color: #bbb;
}

.items {
  margin-top: 0.5rem;
  font-size: 22px;
}

@media only screen and (min-width: 768px) {
  .App {
    height: 100vh;
  }
}

